<template>
    <b-container>
        <div class="header">Some title with episode name</div>

        <!-- <div v-for="(section, index) in sections" :key="index" class="columns">
            {{ section.type }}
        </div> -->

        <b-row
            v-for="(section, index) in sections"
            :key="section.name"
            class="section"
            :class="`section-class-${section.name}`"
        >
            <b-col cols="12" class="section-heading">
                {{ section.name }}
            </b-col>
            <b-col
                v-for="(post, index) in section.posts"
                :key="`${section.name}-${index}`"
                lg="6"
                cols="12"
            >
                <div v-if="section.name == 'Facebook'">
                    <facebook :post="post" />
                </div>

                <div v-else-if="section.name == 'Instagram'">
                    <instagram :post="post" />
                </div>
            </b-col>

            <b-col lg="6" cols="12">
                <div class="empty" @click="addPost(section.name, index)">
                    Add Post
                </div>
            </b-col>
        </b-row>

        <b-row>
            <b-col md="6" cols="12" class="buttons-row">
                <b-button
                    variant="outline-primary"
                    class="w-100"
                    @click="addSection"
                    >Add Section</b-button
                >
                <!-- class="btn btn-primary w-100" -->
            </b-col>
            <b-col md="6" cols="12" class="buttons-row">
                <b-button variant="success" class="w-100" @click="addSection"
                    >Save and Share</b-button
                >
                <!-- class="btn btn-primary w-100" -->
            </b-col>
        </b-row>

        <b-modal id="addPost" title="Add new post">
            <b-form id="addPostForm" @submit.stop.prevent="handleSubmit">
                <b-form-group
                    v-if="postType != 'Instagram'"
                    label="Text"
                    label-for="text-input"
                >
                    <b-form-input
                        id="text-input"
                        v-model="text"
                        required
                    ></b-form-input>
                </b-form-group>
                <b-form-group label="Image url" label-for="image-url-input">
                    <b-form-input
                        id="image-url-input"
                        type="url"
                        v-model="image"
                        :required="postType=='Instagram' && !(this.image || this.video)"
                    ></b-form-input>
                </b-form-group>

                <div class="center">--or--</div>

                <b-form-group label="Video url" label-for="video-url-input">
                    <b-form-input
                        id="video-url-input"
                        type="url"
                        v-model="video"
                        :required="postType=='Instagram' && !(this.image || this.video)"
                    ></b-form-input>
                </b-form-group>

            </b-form>
            <template #modal-footer="{ cancel }">
                <!-- <b-btn @click="cancel">Cancel</b-btn> -->
                <!-- <b-btn variant="primary" type="submit" form="my-form">OK</b-btn> -->
                <button
                    class="btn btn-danger"
                    @click="cancel"
                    form="addPostForm"
                >
                    Cancel
                </button>
                <button
                    type="submit"
                    class="btn btn-primary"
                    form="addPostForm"
                >
                    OK
                </button>
            </template>
        </b-modal>

        <b-modal id="addSection" title="Add section">
            <b-form id="form" @submit.stop.prevent="handleSubmit">
                <b-form-group label="Section type" label-for="section-input">
                    <b-form-select
                        v-model="selectedSection"
                        :options="sectionOptions"
                        disabled-field="isAdded"
                        text-field="name"
                        value-field="id"
                    ></b-form-select>
                </b-form-group>
            </b-form>
            <template #modal-footer="{ cancel }">
                <!-- <b-btn @click="cancel">Cancel</b-btn> -->
                <!-- <b-btn variant="primary" type="submit" form="my-form">OK</b-btn> -->
                <button class="btn btn-danger" @click="cancel" form="form">
                    Cancel
                </button>
                <button type="submit" class="btn btn-primary" form="form">
                    OK
                </button>
            </template>
        </b-modal>
    </b-container>
</template>

<script>
import {
    BContainer,
    BRow,
    BCol,
    BModal,
    BFormGroup,
    BFormInput,
    BForm,
    BFormSelect,
    BButton,
} from "bootstrap-vue";
import Facebook from "../Facebook/Facebook.vue";
import Instagram from "../Instagram/Instagram.vue";
export default {
    components: {
        BContainer,
        BRow,
        BCol,
        BModal,
        BForm,
        BFormGroup,
        BFormInput,
        BFormSelect,
        BButton,
        Facebook,
        Instagram,
    },
    data() {
        return {
            text: "",
            image: "",
            video: "",
            postType: "",
            currSecIndex: "",
            selectedSection: null,

            sections: [
            ],
            sectionOptions: [
                {
                    id:0,
                    name: "Facebook",
                    isAdded: false,
                },
                {
                    id:1,
                    name: "Instagram",
                    isAdded: false,
                },
            ],
            facebookPosts: {
                text: null,
                image: null,
                video: null,
            },
            instagramPosts: {
                image: null,
                video: null,
            },
        };
    },
    methods: {
        addPost(section, index) {
            // console.log("section", section);
            this.currSecIndex = index;
            this.postType = section;
            this.formType = "addPostForm";
            this.$bvModal.show("addPost");
        },
        cancel(){
            console.log("cancelled")
        },
        addSection() {
            this.formType = "addSectionForm";
            this.$bvModal.show("addSection");
        },
        handleSubmit() {
            if (this.formType == "addPostForm") this.handleAddPost();
            else if (this.formType == "addSectionForm") this.handleAddSection();
        },
        handleAddSection() {
            // console.log("here?", this.selectedSection)
            let section = this.sectionOptions[this.selectedSection]
            this.sections.push({
                name:section.name,
                posts:[]
            })
            section.isAdded = true
            this.selectedSection=null
            this.$bvModal.hide("addSection");
        },
        handleAddPost() {
            let temp = null;
            if (this.postType == "Facebook") {
                temp = JSON.parse(JSON.stringify(this.facebookPosts));
                temp.text = this.text;
                temp.image = this.image;
                temp.video = this.video;
            } else if (this.postType == "Instagram") {
                temp = JSON.parse(JSON.stringify(this.instagramPosts));
                temp.image = this.image;
                temp.video = this.video;
            }
            this.sections[this.currSecIndex].posts.push(temp);
            this.clearFields();
            this.$bvModal.hide("addPost");
        },
        clearFields() {
            this.text = null;
            this.video = null;
            this.image = null;
            this.formType = null;
            this.postType = null;
            this.currSecIndex = null;
        },
    },
    watch: {
        video() {
            this.image = null;
        },
        image() {
            this.video = null;
        }
    },
};
</script>

<style scoped>
.header {
    text-align: center;
    margin-bottom: 20px;
    font-size: 30px;
}
.empty {
    min-height: 500px;
    height: calc(100% - 20px);
    border: dotted;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    cursor: pointer;
    margin-bottom: 20px;
}
.section {
    margin-bottom: 20px;
    border-bottom: 2px dotted black;
}
.section-heading {
    margin-bottom: 10px;
    font-size: 30px;
    font-weight: 700px;
}
.center {
    width: 100%;
    text-align: center;
    font-size: 15px;
    color: #aaa;
}
.buttons-row {
    margin-bottom: 20px;
}
</style>


