<template>
  <div id="app" class="container">
    <router-view/>
    <!-- <b-loading :is-full-page="true" :active.sync="isLoading" :can-cancel="false"/> -->
    <div id="progressBar" v-if="isLoading">
      <b-progress v-if="view1Progress!=100" type="is-success" :value="view1Progress" show-value ></b-progress>
      <b-progress v-if="view2Progress!=100" type="is-success" :value="view2Progress" show-value ></b-progress>
      <b-progress v-if="view3Progress!=100" type="is-success" :value="view3Progress" show-value ></b-progress>
      <b-progress v-if="view4Progress!=100" type="is-success" :value="view4Progress" show-value ></b-progress>
    </div>
  </div>
</template>

<script>
// import Header from './components/Header/Header'; 
export default {
  name: 'App',
  created() {
    // Check for token in LocalStorage 
    // this.$store.dispatch('autoLogin')
    this.$store.dispatch('setIsLoading',{value:false})
  },
  computed:{
    isLoading(){
      // console.log('isLoading changed',this.$store.getters.isLoading);
      return this.$store.getters.isLoading || false;
    }
  },
  methods:{
    async init(){
      const authToken = this.$store.getters.getAuthToken
    }
  }
};
</script>

<style>
body{
  font-family: Arial, Helvetica, sans-serif !important;
  background: #f0f2f5 !important;
}
.is-fullwidth, .upload-draggable{
  width:100%;
}
.icon.is-small{
  height: 16px;
  width: 16px;
}
</style>



