                        fill="none"
                        points="20 21 12 13.44 4 21 4 3 20 3 20 21"
                        stroke="currentColor"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                        stroke-width="2"
                    ></polygon>
                </svg>
            </div>
        </div>
        <div class="instagram-post-like-count">1,523 likes</div>
