</template>

<script>
import SvgIcon from "@jamescoyle/vue-icon";
// import { mdiCommentOutline, mdiHeartOutline, mdiShareOutline } from "@mdi/js";
export default {
    components: {
        SvgIcon,
    },
    data() {
        return {
            // comment: mdiCommentOutline,
            // like: mdiHeartOutline,
            // share: mdiShareOutline,
        };
    },
    props: {
        post:Object
    }
};
