        sans-serif;
    padding: 12px 16px 0;
    margin: 0 0 12px;
}
.instagram-heading img {
    height: 32px;
    width: 32px;
    border-radius: 50%;
    margin-right: 10px;
}
.instagram-heading-name {
    font-size: 14px;
    font-weight: 800;
}
.instagram-heading-location {
    font-size: 12px;
}
.instagram-body-text {
    padding: 4px 16px 16px;
    font-size: 15px;
}

.instagram-body-image {
    max-height: 500px;
    overflow: hidden;
}
.instagram-body img {
    width: 100%;
    /* max-height: 500px; */
}
.instagram-body video {
    width: 100%;
}
.instagram-footer {
    /* border-bottom: solid #BEC3C9 1px; */
    /* margin: 0 16px; */
    display: flex;
    justify-content: space-between;
}
.instagram-footer-container {
    /* display: flex;
        align-items: center; */
    padding: 8px 16px 8px 0;
}
.footer-left {
    display: flex;
}
.footer-right {
    padding-top: 8px;
}
.instagram-post-like-count {
    font-weight: 800;
}
</style>


